import React from "react";
import PropTypes from "prop-types";
import { platform, IOS, Div, Group, Button, Epic } from "@vkontakte/vkui";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import HeaderButton from "@vkontakte/vkui/dist/components/HeaderButton/HeaderButton";
import Icon28ChevronBack from "@vkontakte/icons/dist/28/chevron_back";
import Icon24Back from "@vkontakte/icons/dist/24/back";

import "./css/chitos.css";

const osName = platform();

const Chitos = ({ id, go, fetchedUser }) => (
  <Panel id={id}>
    <PanelHeader
      left={
        <HeaderButton onClick={go} data-to="home">
          {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
        </HeaderButton>
      }
    >
      Persik
    </PanelHeader>
    <Div className="section">
      <Group>
        <article>
          <h1 id="logo">
            <span></span>
          </h1>
          <h2>14 мест, которые нужно посетить </h2>
          <h2>Старый город</h2>
          <p>
            «Хочу окончить четверть без троек», «Желаю маме выздороветь»,
            «Мечтаю встретить любовь в этом году», «Пусть не будет войны» — в
            записках, которые вкладывают в щели Стены Плача, просят о разном.
            Еще на камнях Котеля (так Стена Плача называется на иврите) раньше
            выбивали имена больных в надежде на помощь свыше. Вера в чудо и
            сегодня приводит сюда людей из всех уголков мира. Чтобы проникнуть в
            самую суть, спуститесь с гидом в подземные туннели Западной стены,
            где чувствуется дух Святая Святых Иерусалимского Храма. Храмовая
            Гора — священное место и для мусульман. Здесь сверкает золотой Купол
            Скалы и высится первая кибла в исламе — Аль-Акса, сопоставимая по
            значимости со святынями Мекки и Медины. Наконец, Иерусалим
            почитается как колыбель христианской веры. Тут стоит Храм Гроба
            Господня, и две тысячи лет паломники со всего света приходят
            поклониться месту распятия и воскресения Христа.
          </p>

          <h2>Гефсиманский сад</h2>

          <p>
            В небольшом саду в Кедронской долине пробирает до мурашек даже в
            знойную погоду. Только представьте: стоятьу Церкви Всех Наций на том
            самом месте, где Иисус молился в ночь ареста, возле олив, которым
            больше 2000 лет! На Масличной горе немало других памятников,
            посвященных евангельским событиям. У ее подножия расположились
            Гробница Богородицы с пещерной церковью и Гефсиманский грот, где
            капли кровавого пота Христа растопили камень. Поднимитесь чуть выше
            в гору, чтобы полюбоваться русской православной церковью Марии
            Магдалины с иконами и росписями кисти Василия Верещагина и Сергея
            Иванова. Расстояния в Израиле смешные — от Эйлата до Цфата меньше
            500 км. Дороги отличные, руль — на правильной стороне. Все
            располагает к тому, чтобы взять машину и за неделю-другую
            исследовать все легендарные уголки страны, увидеть намного больше
            нашей программы-минимум.
          </p>
          <h2>Музей Израиля</h2>

          <p>
            Более 500 000 экспонатов!{" "}
            <a
              href="http://www.english.imjnet.org.il/page_2428"
              target="_blank"
              rel="noreferrer noopener"
              aria-label=" (opens in a new tab)"
            >
              Музей Израиля{" "}
            </a>
            в Иерусалиме славится огромной коллекцией археологических находок,
            объектов иудаики и этнографии, а также шедевров еврейского и
            мирового искусства. Тут есть все — от Сада искусств Билли Роуза со
            скульптурами Родена и Пикассо до свитков Мертвого моря (они же
            Кумранские рукописи с самыми древними из дошедших до нас библейскими
            текстами) и Макета Второго Храма, в котором Иерусалим предстает
            таким, каким был в 66 году до нашей эры.
          </p>
          <p></p>
        </article>
        <Div>
          <Button size="xl" level="2" onClick={go} data-to="kakas">
            Test
          </Button>
          <hr color="white"></hr>
          <Button size="xl" level="2" onClick={go} data-to="kakas">
            Test
          </Button>
        </Div>
      </Group>
    </Div>
  </Panel>
);

Chitos.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

export default Chitos;
